export const uploadAPI = {
  uploadVideo: async (videoFile) => {
    const formData = new FormData();
    formData.append('video', videoFile);
    
    // Get token from localStorage for Authorization header
    const token = localStorage.getItem('auth_token');
    const headers = {
      'Content-Type': 'multipart/form-data',
    };
    if (token) {
      headers['Authorization'] = `Bearer ${token}`;
    }
    
    const response = await axios.post(`${API_BASE_URL}/api/upload/video`, formData, {
      withCredentials: true,
      headers: headers,
      timeout: 300000, // 5 minutes for large video uploads
      onUploadProgress: (progressEvent) => {
        const percentCompleted = Math.round((progressEvent.loaded * 100) / progressEvent.total);
        console.log(`Upload progress: ${percentCompleted}%`);
      },
    });
    return response.data;
  },
  
  uploadImage: async (imageFile) => {
    const formData = new FormData();
    formData.append('image', imageFile);
    
    // Get token from localStorage for Authorization header
    const token = localStorage.getItem('auth_token');
    const headers = {
      'Content-Type': 'multipart/form-data',
    };
    if (token) {
      headers['Authorization'] = `Bearer ${token}`;
    }
    
    const response = await axios.post(`${API_BASE_URL}/api/upload/image`, formData, {
      withCredentials: true,
      headers: headers,
      timeout: 120000, // 2 minutes for image uploads
      onUploadProgress: (progressEvent) => {
        const percentCompleted = Math.round((progressEvent.loaded * 100) / progressEvent.total);
        console.log(`Image upload progress: ${percentCompleted}%`);
      },
    });
    return response.data;
  },
};

