import React, { useState, useEffect } from 'react';
import { useNavigate, useSearchParams } from 'react-router-dom';
import { Bell, MessageCircle, Upload, Video, Image as ImageIcon, X, Loader, CheckCircle2, AlertCircle } from 'lucide-react';
import { subscriptionAPI, providerAPI, listingAPI, categoriesAPI, uploadAPI } from '../utils/api';
import SubscriptionModal from '../components/SubscriptionModal';
import { toast } from 'react-hot-toast';

const UploadListing = () => {
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const listingType = searchParams.get('type') || 'PRODUCT';

  const [subscriptionStatus, setSubscriptionStatus] = useState(null);
  const [provider, setProvider] = useState(null);
  const [categories, setCategories] = useState([]);
  const [showSubscriptionModal, setShowSubscriptionModal] = useState(false);
  const [loading, setLoading] = useState(true);
  const [uploading, setUploading] = useState(false);

  const [formData, setFormData] = useState({
    type: listingType,
    product_name: '',
    price: '',
    description: '',
    thumbnail_url: '',
    video_url: '',
    link: '',
    category_id: ''
  });

  const [videoPreview, setVideoPreview] = useState(null);
  const [thumbnailPreview, setThumbnailPreview] = useState(null);
  const [videoUploadToken, setVideoUploadToken] = useState(null);
  const [thumbnailUploadToken, setThumbnailUploadToken] = useState(null);
  const [uploadingThumbnail, setUploadingThumbnail] = useState(false);
  const [uploadingVideo, setUploadingVideo] = useState(false);

  // Validation errors
  const [errors, setErrors] = useState({});

  useEffect(() => {
    loadData();
  }, []);

  const loadData = async () => {
    try {
      setLoading(true);
      const [subStatus, categoriesData] = await Promise.all([
        subscriptionAPI.getStatus(),
        categoriesAPI.getAll()
      ]);

      setSubscriptionStatus(subStatus);
      setCategories(categoriesData.categories || []);

      try {
        const providerData = await providerAPI.getMe();
        setProvider(providerData.provider);
      } catch (error) {
        console.log('No provider profile found');
      }
    } catch (error) {
      console.error('Error loading data:', error);
      toast.error('Failed to load data');
    } finally {
      setLoading(false);
    }
  };

  const validateForm = () => {
    const newErrors = {};

    if (!formData.video_url || !videoUploadToken) {
      newErrors.video = 'Video is required';
    }
    if (!formData.thumbnail_url || !thumbnailUploadToken) {
      newErrors.thumbnail = 'Thumbnail is required';
    }
    if (!formData.product_name.trim()) {
      newErrors.product_name = 'Product/Service name is required';
    }
    if (!formData.price || parseFloat(formData.price) <= 0) {
      newErrors.price = 'Valid price is required';
    }
    if (!formData.category_id) {
      newErrors.category_id = 'Category is required';
    }
    if (!formData.description.trim()) {
      newErrors.description = 'Description is required';
    }
    if (!formData.link.trim()) {
      newErrors.link = 'Link is required';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleVideoChange = async (e) => {
    const file = e.target.files[0];
    if (!file) return;

    const maxVideoSize = 500; // MB
    if (file.size > maxVideoSize * 1024 * 1024) {
      toast.error(`Video file too large. Maximum size is ${maxVideoSize}MB`);
      return;
    }

    const allowedTypes = ['video/mp4', 'video/mov', 'video/avi', 'video/webm', 'video/mkv'];
    const allowedExtensions = ['mp4', 'mov', 'avi', 'webm', 'mkv'];
    const fileExt = file.name.split('.').pop()?.toLowerCase();
    
    if (!allowedExtensions.includes(fileExt) && !allowedTypes.includes(file.type)) {
      toast.error(`Invalid file type. Allowed formats: ${allowedExtensions.join(', ')}`);
      return;
    }

    setUploadingVideo(true);
    setErrors(prev => ({ ...prev, video: null }));

    try {
      const response = await uploadAPI.uploadVideo(file);
      
      if (response.ok && response.url) {
        const videoUrl = response.url;
        setFormData(prevFormData => ({ ...prevFormData, video_url: videoUrl }));
        setVideoPreview(URL.createObjectURL(file));
        setVideoUploadToken(response.success_token || null);
        toast.success('Video uploaded successfully!', { duration: 3000 });
      } else {
        throw new Error(response.message || 'Upload failed');
      }
    } catch (error) {
      console.error('Video upload error:', error);
      toast.error(
        error.response?.data?.message || error.message || 'Failed to upload video'
      );
      e.target.value = '';
    } finally {
      setUploadingVideo(false);
    }
  };

  const handleThumbnailChange = async (e) => {
    const file = e.target.files[0];
    if (!file) return;

    const maxImageSize = 20; // MB
    if (file.size > maxImageSize * 1024 * 1024) {
      toast.error(`Image file too large. Maximum size is ${maxImageSize}MB`);
      return;
    }

    const allowedTypes = ['image/jpeg', 'image/jpg', 'image/png', 'image/gif', 'image/webp'];
    const allowedExtensions = ['jpg', 'jpeg', 'png', 'gif', 'webp'];
    const fileExt = file.name.split('.').pop()?.toLowerCase();
    
    if (!allowedExtensions.includes(fileExt) && !allowedTypes.includes(file.type)) {
      toast.error(`Invalid file type. Allowed formats: ${allowedExtensions.join(', ')}`);
      return;
    }

    setUploadingThumbnail(true);
    setErrors(prev => ({ ...prev, thumbnail: null }));

    try {
      const response = await uploadAPI.uploadImage(file);
      
      if (response.ok && response.url) {
        setFormData(prevFormData => ({ ...prevFormData, thumbnail_url: response.url }));
        setThumbnailPreview(URL.createObjectURL(file));
        setThumbnailUploadToken(response.success_token || null);
        toast.success('Thumbnail uploaded successfully!', { duration: 3000 });
      } else {
        throw new Error(response.message || 'Upload failed');
      }
    } catch (error) {
      console.error('Thumbnail upload error:', error);
      toast.error(
        error.response?.data?.message || error.message || 'Failed to upload thumbnail'
      );
      e.target.value = '';
    } finally {
      setUploadingThumbnail(false);
    }
  };

  const removeVideo = () => {
    setFormData(prev => ({ ...prev, video_url: '' }));
    setVideoPreview(null);
    setVideoUploadToken(null);
    if (videoPreview) {
      URL.revokeObjectURL(videoPreview);
    }
  };

  const removeThumbnail = () => {
    setFormData(prev => ({ ...prev, thumbnail_url: '' }));
    setThumbnailPreview(null);
    setThumbnailUploadToken(null);
    if (thumbnailPreview) {
      URL.revokeObjectURL(thumbnailPreview);
    }
  };

  const handleInputChange = (field, value) => {
    setFormData(prev => ({ ...prev, [field]: value }));
    // Clear error when user starts typing
    if (errors[field]) {
      setErrors(prev => ({ ...prev, [field]: null }));
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    // Validate all fields
    if (!validateForm()) {
      toast.error('Please fill in all required fields');
      return;
    }

    if (!provider) {
      toast.error('Please create a provider profile first');
      navigate('/provider/register');
      return;
    }

    if (subscriptionStatus.uploads_remaining === 0 && !subscriptionStatus.is_subscribed) {
      toast.error('Upload limit reached. Please subscribe!');
      setShowSubscriptionModal(true);
      return;
    }

    setUploading(true);
    try {
      const response = await listingAPI.create(formData);
      if (response.ok && response.message) {
        toast.success('Listing request submitted successfully! It will be reviewed by admin.');
        // Reset form
        setFormData({
          type: listingType,
          product_name: '',
          price: '',
          description: '',
          thumbnail_url: '',
          video_url: '',
          link: '',
          category_id: ''
        });
        setVideoPreview(null);
        setThumbnailPreview(null);
        setVideoUploadToken(null);
        setThumbnailUploadToken(null);
        setErrors({});
        // Navigate to my listings after a delay
        setTimeout(() => {
          navigate('/listing/my');
        }, 2000);
      } else {
        toast.error(response.error || 'Failed to submit listing request');
      }
    } catch (error) {
      console.error('Error creating listing:', error);
      toast.error(error.response?.data?.error || 'Failed to create listing request');
    } finally {
      setUploading(false);
    }
  };

  const isFormValid = () => {
    return (
      formData.video_url &&
      videoUploadToken &&
      formData.thumbnail_url &&
      thumbnailUploadToken &&
      formData.product_name.trim() &&
      formData.price &&
      parseFloat(formData.price) > 0 &&
      formData.category_id &&
      formData.description.trim() &&
      formData.link.trim()
    );
  };

  if (loading) {
    return (
      <div className="min-h-screen bg-white flex items-center justify-center">
        <Loader className="w-8 h-8 animate-spin text-[#6C4DFF]" />
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-white">
      {/* Header */}
      <header className="w-full bg-white border-b border-gray-200 sticky top-0 z-50">
        <div className="px-4 sm:px-5 py-3.5 sm:py-4 flex items-center justify-between">
          <div className="flex items-center">
            <span className="text-2xl sm:text-[24px] font-gilroy font-bold">
              <span className="text-black">Get</span>
              <span className="text-[#6C4DFF]">nayi</span>
            </span>
          </div>
          <div className="flex items-center gap-3 sm:gap-4">
            <button className="text-gray-600 hover:text-gray-900 transition-colors">
              <Bell className="w-5 h-5 sm:w-6 sm:h-6" />
            </button>
            <button className="text-gray-600 hover:text-gray-900 transition-colors">
              <MessageCircle className="w-5 h-5 sm:w-6 sm:h-6" />
            </button>
          </div>
        </div>
      </header>

      <div className="px-4 sm:px-5 py-6 sm:py-8 pb-32 sm:pb-12 max-w-2xl mx-auto">
        {/* Subscription Status */}
        {subscriptionStatus && !subscriptionStatus.is_subscribed && (
          <div className="mb-6 p-4 bg-purple-50 border border-purple-200 rounded-xl">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-purple-900 font-inter">
                  Free Plan
                </p>
                <p className="text-xs text-purple-700 font-inter mt-1">
                  Uploads remaining: {subscriptionStatus.uploads_remaining}/5
                </p>
              </div>
              <button
                onClick={() => setShowSubscriptionModal(true)}
                className="text-xs font-medium text-[#6C4DFF] hover:text-[#5a3ddb] font-inter underline"
              >
                Upgrade
              </button>
            </div>
          </div>
        )}

        <form onSubmit={handleSubmit} className="space-y-6">
          {/* Video Upload Section */}
          <div>
            <label className="block text-sm font-medium text-gray-900 mb-2 font-inter">
              Video <span className="text-red-500">*</span>
            </label>
            <label className="block cursor-pointer">
              <div className="relative w-full h-48 bg-gradient-to-br from-[#E8D5FF] to-[#F3E8FF] rounded-2xl border-2 border-dashed border-[#6C4DFF] flex items-center justify-center transition-all hover:border-[#5a3ddb] hover:bg-[#F0E5FF]">
                {uploadingVideo ? (
                  <div className="flex flex-col items-center gap-3">
                    <Loader className="w-8 h-8 animate-spin text-[#6C4DFF]" />
                    <p className="text-[#6C4DFF] font-medium text-sm font-inter">Uploading video...</p>
                  </div>
                ) : videoPreview ? (
                  <div className="relative w-full h-full p-4">
                    <video src={videoPreview} controls className="w-full h-full object-contain rounded-lg" />
                    <button
                      type="button"
                      onClick={(e) => {
                        e.preventDefault();
                        e.stopPropagation();
                        removeVideo();
                      }}
                      className="absolute top-2 right-2 bg-red-500 text-white rounded-full p-2 hover:bg-red-600 transition-colors"
                    >
                      <X className="w-4 h-4" />
                    </button>
                    {videoUploadToken && (
                      <div className="absolute bottom-2 left-2 bg-green-500 text-white px-3 py-1 rounded-full flex items-center gap-1 text-xs font-medium">
                        <CheckCircle2 className="w-3 h-3" />
                        Uploaded
                      </div>
                    )}
                  </div>
                ) : (
                  <div className="flex flex-col items-center gap-3">
                    <div className="w-16 h-16 bg-[#6C4DFF] rounded-full flex items-center justify-center">
                      <Video className="w-8 h-8 text-white" />
                    </div>
                    <div className="text-center">
                      <p className="text-[#6C4DFF] font-semibold text-base font-inter">+ Add your Video</p>
                      <p className="text-gray-500 text-xs mt-1 font-inter">MP4, MOV, AVI, WEBM, MKV up to 500MB</p>
                    </div>
                  </div>
                )}
              </div>
              <input
                type="file"
                accept="video/*"
                onChange={handleVideoChange}
                className="hidden"
                disabled={uploadingVideo || (videoUploadToken !== null)}
              />
            </label>
            {errors.video && (
              <p className="text-xs text-red-500 mt-1 flex items-center gap-1 font-inter">
                <AlertCircle className="w-3 h-3" />
                {errors.video}
              </p>
            )}
          </div>

          {/* Thumbnail Upload Section */}
          <div>
            <label className="block text-sm font-medium text-gray-900 mb-2 font-inter">
              Thumbnail Image <span className="text-red-500">*</span>
            </label>
            <label className="block cursor-pointer">
              <div className="relative w-full h-48 bg-gradient-to-br from-[#E8D5FF] to-[#F3E8FF] rounded-2xl border-2 border-dashed border-[#6C4DFF] flex items-center justify-center transition-all hover:border-[#5a3ddb] hover:bg-[#F0E5FF]">
                {uploadingThumbnail ? (
                  <div className="flex flex-col items-center gap-3">
                    <Loader className="w-8 h-8 animate-spin text-[#6C4DFF]" />
                    <p className="text-[#6C4DFF] font-medium text-sm font-inter">Uploading thumbnail...</p>
                  </div>
                ) : thumbnailPreview ? (
                  <div className="relative w-full h-full p-4">
                    <img src={thumbnailPreview} alt="Thumbnail" className="w-full h-full object-cover rounded-lg" />
                    <button
                      type="button"
                      onClick={(e) => {
                        e.preventDefault();
                        e.stopPropagation();
                        removeThumbnail();
                      }}
                      className="absolute top-2 right-2 bg-red-500 text-white rounded-full p-2 hover:bg-red-600 transition-colors"
                    >
                      <X className="w-4 h-4" />
                    </button>
                    {thumbnailUploadToken && (
                      <div className="absolute bottom-2 left-2 bg-green-500 text-white px-3 py-1 rounded-full flex items-center gap-1 text-xs font-medium">
                        <CheckCircle2 className="w-3 h-3" />
                        Uploaded
                      </div>
                    )}
                  </div>
                ) : (
                  <div className="flex flex-col items-center gap-3">
                    <div className="w-16 h-16 bg-[#6C4DFF] rounded-full flex items-center justify-center">
                      <ImageIcon className="w-8 h-8 text-white" />
                    </div>
                    <div className="text-center">
                      <p className="text-[#6C4DFF] font-semibold text-base font-inter">+ Add your Image</p>
                      <p className="text-gray-500 text-xs mt-1 font-inter">JPG, PNG, GIF, WEBP up to 20MB</p>
                    </div>
                  </div>
                )}
              </div>
              <input
                type="file"
                accept="image/*"
                onChange={handleThumbnailChange}
                className="hidden"
                disabled={uploadingThumbnail || (thumbnailUploadToken !== null)}
              />
            </label>
            {errors.thumbnail && (
              <p className="text-xs text-red-500 mt-1 flex items-center gap-1 font-inter">
                <AlertCircle className="w-3 h-3" />
                {errors.thumbnail}
              </p>
            )}
          </div>

          {/* Form Fields */}
          <div className="space-y-5">
            {/* Product/Service Name */}
            <div>
              <label className="block text-sm font-medium text-gray-900 mb-2 font-inter">
                {listingType === 'PRODUCT' ? 'Product' : 'Service'} Name <span className="text-red-500">*</span>
              </label>
              <input
                type="text"
                value={formData.product_name}
                onChange={(e) => handleInputChange('product_name', e.target.value)}
                className={`w-full px-4 py-3 border rounded-xl focus:outline-none focus:ring-2 focus:ring-[#6C4DFF] transition-all font-inter text-gray-900 placeholder-gray-400 ${
                  errors.product_name ? 'border-red-500' : 'border-gray-300'
                }`}
                placeholder="Enter product/service name"
              />
              {errors.product_name && (
                <p className="text-xs text-red-500 mt-1 flex items-center gap-1 font-inter">
                  <AlertCircle className="w-3 h-3" />
                  {errors.product_name}
                </p>
              )}
            </div>

            {/* Price & Category */}
            <div className="grid grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-medium text-gray-900 mb-2 font-inter">
                  Price () <span className="text-red-500">*</span>
                </label>
                <input
                  type="number"
                  min="0"
                  step="0.01"
                  value={formData.price}
                  onChange={(e) => handleInputChange('price', e.target.value)}
                  className={`w-full px-4 py-3 border rounded-xl focus:outline-none focus:ring-2 focus:ring-[#6C4DFF] transition-all font-inter text-gray-900 placeholder-gray-400 ${
                    errors.price ? 'border-red-500' : 'border-gray-300'
                  }`}
                  placeholder="0.00"
                />
                {errors.price && (
                  <p className="text-xs text-red-500 mt-1 flex items-center gap-1 font-inter">
                    <AlertCircle className="w-3 h-3" />
                    {errors.price}
                  </p>
                )}
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-900 mb-2 font-inter">
                  Category <span className="text-red-500">*</span>
                </label>
                <select
                  value={formData.category_id}
                  onChange={(e) => handleInputChange('category_id', e.target.value)}
                  className={`w-full px-4 py-3 border rounded-xl focus:outline-none focus:ring-2 focus:ring-[#6C4DFF] transition-all font-inter text-gray-900 bg-white ${
                    errors.category_id ? 'border-red-500' : 'border-gray-300'
                  }`}
                >
                  <option value="">Select category</option>
                  {categories.map(cat => (
                    <option key={cat.category_id} value={cat.category_id}>
                      {cat.category_name}
                    </option>
                  ))}
                </select>
                {errors.category_id && (
                  <p className="text-xs text-red-500 mt-1 flex items-center gap-1 font-inter">
                    <AlertCircle className="w-3 h-3" />
                    {errors.category_id}
                  </p>
                )}
              </div>
            </div>

            {/* Description */}
            <div>
              <label className="block text-sm font-medium text-gray-900 mb-2 font-inter">
                Description <span className="text-red-500">*</span>
              </label>
              <textarea
                rows={4}
                value={formData.description}
                onChange={(e) => handleInputChange('description', e.target.value)}
                className={`w-full px-4 py-3 border rounded-xl focus:outline-none focus:ring-2 focus:ring-[#6C4DFF] transition-all font-inter text-gray-900 placeholder-gray-400 resize-none ${
                  errors.description ? 'border-red-500' : 'border-gray-300'
                }`}
                placeholder="Describe your product or service"
              />
              {errors.description && (
                <p className="text-xs text-red-500 mt-1 flex items-center gap-1 font-inter">
                  <AlertCircle className="w-3 h-3" />
                  {errors.description}
                </p>
              )}
            </div>

            {/* Link */}
            <div>
              <label className="block text-sm font-medium text-gray-900 mb-2 font-inter">
                Link (Website/Store) <span className="text-red-500">*</span>
              </label>
              <input
                type="url"
                value={formData.link}
                onChange={(e) => handleInputChange('link', e.target.value)}
                className={`w-full px-4 py-3 border rounded-xl focus:outline-none focus:ring-2 focus:ring-[#6C4DFF] transition-all font-inter text-gray-900 placeholder-gray-400 ${
                  errors.link ? 'border-red-500' : 'border-gray-300'
                }`}
                placeholder="https://your-website.com"
              />
              {errors.link && (
                <p className="text-xs text-red-500 mt-1 flex items-center gap-1 font-inter">
                  <AlertCircle className="w-3 h-3" />
                  {errors.link}
                </p>
              )}
            </div>
          </div>

          {/* Submit Button */}
          <div className="pt-4">
            <button
              type="submit"
              disabled={uploading || !isFormValid()}
              className="w-full bg-[#6C4DFF] text-white py-4 rounded-xl font-semibold text-base font-inter shadow-lg hover:shadow-xl active:scale-[0.98] transition-all disabled:opacity-50 disabled:cursor-not-allowed disabled:active:scale-100"
            >
              {uploading ? (
                <span className="flex items-center justify-center gap-2">
                  <Loader className="w-5 h-5 animate-spin" />
                  Creating Listing...
                </span>
              ) : (
                "Submit Listing"
              )}
            </button>
            {!isFormValid() && (
              <p className="text-xs text-gray-500 text-center mt-2 font-inter">
                Please fill in all required fields to submit
              </p>
            )}
          </div>
        </form>
      </div>

      {/* Subscription Modal */}
      {showSubscriptionModal && (
        <SubscriptionModal
          onClose={() => setShowSubscriptionModal(false)}
          onSuccess={() => {
            setShowSubscriptionModal(false);
            loadData();
          }}
        />
      )}
    </div>
  );
};

export default UploadListing;
