import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { Clock, CheckCircle, XCircle, RefreshCw, ArrowLeft, Loader } from 'lucide-react';
import { providerAPI } from '../utils/api';
import { toast } from 'react-hot-toast';
import Header from '../components/Header';

const ProviderRequestWaiting = () => {
  const navigate = useNavigate();
  const [requestStatus, setRequestStatus] = useState(null);
  const [loading, setLoading] = useState(true);
  const [autoRefresh, setAutoRefresh] = useState(true);

  useEffect(() => {
    checkStatus();
    const interval = setInterval(() => {
      if (autoRefresh) {
        checkStatus();
      }
    }, 5000); // Check every 5 seconds

    return () => clearInterval(interval);
  }, [autoRefresh]);

  const checkStatus = async () => {
    try {
      const response = await providerAPI.getRequestStatus();
      if (response.ok) {
        if (response.has_provider) {
          navigate('/listing');
          return;
        }
        setRequestStatus(response.request_status);
        if (response.request_status === 'APPROVED') {
          setAutoRefresh(false);
          toast.success('Your provider request has been approved!');
          setTimeout(() => navigate('/listing'), 2000);
        }
      }
    } catch (error) {
      console.error('Error checking request status:', error);
    } finally {
      setLoading(false);
    }
  };

  if (loading) {
    return (
      <div className="min-h-screen bg-black star-particles flex items-center justify-center">
        <Loader className="w-8 h-8 animate-spin text-purple-500" />
      </div>
    );
  }

  const getStatusIcon = () => {
    switch (requestStatus) {
      case 'APPROVED':
        return <CheckCircle className="w-16 h-16 text-green-500" />;
      case 'REJECTED':
        return <XCircle className="w-16 h-16 text-red-500" />;
      default:
        return <Clock className="w-16 h-16 text-yellow-500 animate-pulse" />;
    }
  };

  const getStatusMessage = () => {
    switch (requestStatus) {
      case 'APPROVED':
        return {
          title: 'Request Approved!',
          message: 'Your provider request has been approved. You can now start creating listings.',
          color: 'text-green-500'
        };
      case 'REJECTED':
        return {
          title: 'Request Rejected',
          message: 'Your provider request has been rejected. Please contact support or submit a new request.',
          color: 'text-red-500'
        };
      default:
        return {
          title: 'Request Pending',
          message: 'Your provider request is under review. We will notify you once a decision has been made.',
          color: 'text-yellow-500'
        };
    }
  };

  const statusInfo = getStatusMessage();

  return (
    <div className="min-h-screen bg-black star-particles">
      <Header />
      
      <div className="pt-16 pb-32 sm:pb-24 px-4 max-w-2xl mx-auto">
        <button
          onClick={() => navigate('/')}
          className="flex items-center gap-2 text-gray-300 hover:text-white mb-6 transition-colors"
        >
          <ArrowLeft className="w-5 h-5" />
          <span className="font-inter">Back to Home</span>
        </button>

        <div className="bg-gray-800 border border-gray-700 rounded-xl shadow-lg p-8 text-center">
          <div className="flex justify-center mb-6">
            {getStatusIcon()}
          </div>

          <h1 className={`text-3xl font-bold mb-4 ${statusInfo.color} font-inter`}>
            {statusInfo.title}
          </h1>

          <p className="text-gray-300 mb-8 font-inter">
            {statusInfo.message}
          </p>

          {requestStatus === 'PENDING' && (
            <>
              <div className="bg-gray-700 rounded-lg p-4 mb-6">
                <div className="flex items-center justify-center gap-2 text-gray-300 mb-2">
                  <RefreshCw className={`w-4 h-4 ${autoRefresh ? 'animate-spin' : ''}`} />
                  <span className="text-sm font-inter">
                    {autoRefresh ? 'Auto-refreshing...' : 'Auto-refresh paused'}
                  </span>
                </div>
                <p className="text-xs text-gray-400 font-inter">
                  This page will automatically update when your request is reviewed
                </p>
              </div>

              <div className="flex gap-4 justify-center">
                <button
                  onClick={checkStatus}
                  className="px-6 py-3 bg-purple-600 text-white rounded-lg hover:bg-purple-700 transition-colors font-inter"
                >
                  Refresh Status
                </button>
                <button
                  onClick={() => setAutoRefresh(!autoRefresh)}
                  className="px-6 py-3 bg-gray-700 text-gray-300 rounded-lg hover:bg-gray-600 transition-colors font-inter"
                >
                  {autoRefresh ? 'Pause Auto-Refresh' : 'Resume Auto-Refresh'}
                </button>
              </div>
            </>
          )}

          {requestStatus === 'REJECTED' && (
            <div className="flex gap-4 justify-center">
              <button
                onClick={() => navigate('/provider-registration')}
                className="px-6 py-3 bg-purple-600 text-white rounded-lg hover:bg-purple-700 transition-colors font-inter"
              >
                Submit New Request
              </button>
              <button
                onClick={() => navigate('/')}
                className="px-6 py-3 bg-gray-700 text-gray-300 rounded-lg hover:bg-gray-600 transition-colors font-inter"
              >
                Go to Home
              </button>
            </div>
          )}

          {requestStatus === 'APPROVED' && (
            <button
              onClick={() => navigate('/listing')}
              className="px-6 py-3 bg-purple-600 text-white rounded-lg hover:bg-purple-700 transition-colors font-inter"
            >
              Go to Listings
            </button>
          )}
        </div>
      </div>
    </div>
  );
};

export default ProviderRequestWaiting;
